@charset "UTF-8";
/*ГЛОБАЛЬНЫЕ СТИЛИ*/
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Light.woff2") format("woff2"), url("../font/OpenSans-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../font/OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../fonts/OpenSans-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  height: 100%; }

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  color: #000000; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

/*БЛОКИ*/
.page-header {
  min-height: 825px;
  margin-bottom: 150px;
  background-color: #000000;
  background-image: url("../img/promo-desktop.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover; }
  @media (min-resolution: 2dppx) {
    .page-header {
      background-image: url("../img/promo-desktop@2x.jpg"); } }
  @media (max-width: 1023px) {
    .page-header {
      min-height: 340px;
      margin-bottom: 70px;
      background-image: url("../img/promo-tablet.jpg"); } }
  @media (max-width: 1023px) and (min-resolution: 2dppx) {
    .page-header {
      background-image: url("../img/promo-tablet@2x.jpg"); } }
  @media (max-width: 767px) {
    .page-header {
      min-height: 390px;
      background-image: url("../img/promo-mobile.jpg"); } }
  @media (max-width: 767px) and (min-resolution: 2dppx) {
    .page-header {
      background-image: url("../img/promo-mobile@2x.jpg"); } }

.main-navigation {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 874px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .main-navigation {
      max-width: 100%; } }

.main-navigation__toggle {
  display: none; }
  @media (max-width: 1023px) {
    .main-navigation__toggle {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 30;
      display: block;
      width: 50px;
      height: 50px;
      cursor: pointer; } }

@media (max-width: 1023px) {
  .main-navigation--closed .main-navigation__toggle {
    background-color: #0ad9c6; } }

@media (max-width: 1023px) {
  .main-navigation--opened .main-navigation__toggle {
    background-color: #ffffff; } }

@media (max-width: 1023px) {
  .main-navigation--closed .main-navigation__toggle::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 30px;
    height: 30px;
    background-image: url("../img/back-icon-menu-open.svg");
    background-repeat: no-repeat;
    background-position: center; } }

@media (max-width: 1023px) {
  .main-navigation--opened .main-navigation__toggle::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 22px;
    height: 22px;
    background-image: url("../img/back-icon-menu-closed.svg");
    background-repeat: no-repeat;
    background-position: center; } }

@media (max-width: 1023px) {
  .main-navigation--nojs .main-navigation__toggle {
    display: none; } }

.main-navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 256px;
  padding: 21px 20px 9px 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }
  @media (max-width: 1023px) {
    .main-navigation__list {
      position: fixed;
      top: 100px;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 20;
      flex-direction: column;
      order: 2;
      width: 100%;
      margin-top: -100px;
      margin-bottom: 0;
      padding: 0;
      padding-top: 100px;
      background-color: #ffffff;
      border-bottom: none; } }

@media (max-width: 1023px) {
  .main-navigation--closed .main-navigation__list {
    display: none; } }

@media (max-width: 1023px) {
  .main-navigation--nojs .main-navigation__list {
    display: block; } }

@media (max-width: 1023px) {
  .main-navigation__item {
    width: 95.8%;
    margin-bottom: 30px;
    padding: 17px 16px 15px 16px;
    background-color: #f6f6f6; } }

.main-navigation__item:not(:last-child) {
  margin-right: 16%; }
  @media (max-width: 1023px) {
    .main-navigation__item:not(:last-child) {
      margin-right: 0; } }

.main-navigation__item a {
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  color: #ffffff; }
  @media (max-width: 1023px) {
    .main-navigation__item a {
      display: flex;
      justify-content: space-between;
      font-weight: 400;
      font-size: 20px;
      line-height: 140%;
      color: #48494d; } }

@media (max-width: 1023px) {
  .main-navigation__item a::after {
    content: "";
    width: 25px;
    height: 25px;
    margin-right: 17px;
    background-image: url("../img/back-icon-arrow.svg"); } }

@media (max-width: 1023px) {
  .main-navigation__logo-link {
    order: 1;
    width: 68px;
    height: 20px;
    margin-top: 25px;
    margin-bottom: 5px;
    margin-left: 21px; } }

@media (max-width: 767px) {
  .main-navigation__logo-link {
    margin-left: 16px; } }

.main-navigation__logo-link img {
  width: 68px;
  height: 20px; }
